<template>
  <Container :title="title">
    <v-form :advanced="advanced" @submit="submit">
      <template slot="base">
        <group :label="lang.typename" type="select" :options="arctype_opt" v-model="archive.type_id" />
        <group :label="lang.title" type="input" v-model="archive.title"/>
        <group :label="lang.flag" type="inline" v-model="archive.flag"/>
        <group :label="lang.litpic" type="image" v-model="archive.litpic" />
        <group :label="lang.content" type="editor" v-model="archive.content" />
        <group :label="lang.username" type="select" :options="user_opt" v-model="archive.uid" />
      </template>

      <template slot="advanced">
        <group :label="lang.keywords" type="input" v-model="archive.keywords" />
        <group :label="lang.description" type="textarea" v-model="archive.description" />
        <group  type="status" v-model="archive.status" />
        <group :label="lang.click" type="input" :disabled="true" v-model="archive.click" />

        <group :label="$t('status.createTime')" type="input" :disabled="true"
               v-model="archive.create_time" />

        <group :label="$t('status.updateTime')" type="input" :disabled="true" v-model="archive.edit_time" />

      </template>
    </v-form>

  </Container>
</template>

<script>
  import {Container,VForm,Group} from "@/components"

  export default {
    components: {Container, Group, VForm},
    data(){
      return {
        title:this.$t('archive.editTile'),
        advanced:true,
        lang:this.$t('archive')
      }
    },

    computed:{
      archive:{
        set(value){
          this.$store.commit('archive/SET_ARCHIVE', value)
        },
        get(){
          return this.$store.state.archive.archive
        }
      },
      arctype_opt(){
        const options = this.$store.state.arctype.type_options
        if (Array.isArray(options) && options.length > 0){
          const type_id = this.archive.type_id
          if (type_id){
            this.archive.type_id = Number(type_id)
          } else{
            this.archive.type_id = options[0].value
          }
        }
        return options
      },
      user_opt(){
        const options =  this.$store.state.user.user_options
        if (Array.isArray(options) && options.length > 0){
          this.archive.uid = options[0].value
        }
        return options
      }
    },

    created(){
      const id = this.$route.query['id']
      this.$store.dispatch('archive/initial',id)
    },

    methods:{
      submit(){
        this.$store.dispatch('archive/editArchive').then(resp =>{
          if (resp){
            this.$toast.success({
              title:'API 请求成功~',
              message:resp.error
            })
          }
        })
      }
    }
  }
</script>



